.App {
    color: #333;
    background-color: #fff;
    max-height: 800px;
    min-width: 500px;
    padding: 2em 1.5em;
    margin: 2em;
    border: 1px solid #aaa;
    border-radius: 5px;
    -webkit-box-shadow: 0 0 2em rgba(0, 0, 0, 0.2);
            box-shadow: 0 0 2em rgba(0, 0, 0, 0.2);
    font-family: "微软雅黑", '黑体', Arial, Helvetica, sans-serif
}

.App>h1,
.App>button {
    float: left
}

.App>button {
    background-color: #fff;
    color: #333;
    padding: 6px 10px;
    font-size: 12px;
    border: 1px solid #333;
    border-radius: 5px;
    -webkit-transition: 0.1s;
    -o-transition: 0.1s;
    transition: 0.1s;
    margin: 29px 0 0 10px;
}

.App>button:hover {
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
}

.inputItem {
    margin-top: 10px;
}

.App .inputItem li:nth-child(odd) {
    background-color: #eee;
}

.App .inputItem li:nth-child(even) {
    background-color: transparent;
}


